.web{
    display: flex;
    flex: 1;
    align-items: center;
    background-color: #44475a;
        padding-right: 10px;
        

}
.web-option{
    font-size: 20px;
    margin-right: 16px;
    font-weight: 700;
    padding-left: 10px;
}

.web-option a{
    text-decoration: none;
    color: #f8f8f2;
    display: flex;
    align-items: center;
}

.option-icon{
    display: flex;
    align-items: center;
    margin-right: 4px;
}
.web-option:last-child {
    margin-right: 0px;
}