.nav {
    position: fixed;
}
.header{
    display: flex;
    justify-content: space-between;
    flex: 1;
    position: relative;
}
.menu{
    background-color: #44475a;
    display: flex;
    flex: 1;
    justify-content: flex-end;
    
}

.menu-icon{
    font-size: 24px;
}

.mobile-menu{
    display: none;
}
.web-menu{
    display: flex;
}

.logo{
    display: flex;
    font-size: 36px;
    color: #f8f8f2;
    font-weight: 800;
    background-color: #44475a;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.2);
    padding-left: 10px;
}

@media only screen and (max-width: 720px) {
    .mobile-menu{
        display: flex;
    }
    .web-menu{
        display: none;
    }
    .logo{
        font-size: 24px;
    }
}